const payStatusColor = (pay) => {
  if (pay === 0) return { color: "#ffbf09", name: "   " };
  if (pay === 1) return { color: "#ffbf09", name: "    " };
  if (pay === 2) return { color: "#ffbf09", name: "  " };
  if (pay === 3) return { color: "#ffbf09", name: "  " };
  if (pay === 4) return { color: "#ffbf09", name: "  " };
  if (pay === 5) return { color: "#ffbf09", name: " " };
  if (pay === 6)
    return {
      color: "#ffbf09",
      name: "     ",
    };
  if (pay === 7) return { color: "#ffbf09", name: "   " };
};

export default payStatusColor;
