/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect } from "react";
import { useSelector } from "react-redux";

const useLabelName = (label, setLabelName) => {
  const { profile, account, followAccount } = useSelector(
    ({ account }) => account
  );
  const { guestName } = useSelector(({ _MainReducer }) => _MainReducer);

  useEffect(() => {
    let n = "";
    switch (label.mode) {
      case "ads": {
        n = " ";
        break;
      }
      case "adsCats": {
        n = " ";
        break;
      }
      case "ads-single": {
        n = " ";
        break;
      }
      case "rent": {
        n = " ";
        break;
      }
      case "map": {
        n = "";
        break;
      }
      case "rentItem": {
        n = "";
        break;
      }
      case "category": {
        n = " ";
        break;
      }
      case "categoryList": {
        n = " ";
        break;
      }
      case (n = "session"): {
        n = "  ";
        break;
      }
      case "adsComposeNew": {
        n = " ";
        break;
      }
      case "adsComposeEdit": {
        n = " ";
        break;
      }
      case "adsComposeCat": {
        n = " ";
        break;
      }
      case "followers": {
        n = `  ${followAccount.name ?? guestName}`;
        break;
      }
      case "followings": {
        n = `  ${followAccount.name ?? guestName}`;
        break;
      }
      case "notifications": {
        n = " ";
        break;
      }
      case "profile": {
        n =
          profile?.account?.name && profile?.account?.name?.length > 0
            ? profile?.account?.name
            : "";
        break;
      }
      case "features": {
        n = "";
        break;
      }
      case "mediaSearch": {
        n = "  ";
        break;
      }
      case "fareSearch": {
        n = "  ";
        break;
      }
      case "addProduct": {
        n = " ";
        break;
      }
      case "editProduct": {
        n = " ";
        break;
      }
      case "adsFilter": {
        n = " ";
        break;
      }
      case "wallet": {
        n = " ";
        break;
      }
      case "adsSearch": {
        n = "  ";
        break;
      }
      case "editPost": {
        n = " ";
        break;
      }
      case "adsOrder": {
        n = " ";
        break;
      }
      case "favorite": {
        n = "  ";
        break;
      }
      case "movie": {
        n = "";
        break;
      }
      case "addPost": {
        n = "  ";
        break;
      }
      case "myPosts": {
        n = " ";
        break;
      }
      case "myProducts": {
        n = " ";
        break;
      }
      case "submitBasket": {
        n = "    ";
        break;
      }
      default:
        return;
    }
    setLabelName(n);
  }, [account.name, label, profile?.account?.name, followAccount]);
};

export default useLabelName;
