import Setting from "components/Settings/Setting";
import BarLayout from "layouts/BarLayout";
import { Helmet } from "react-helmet";

const Settings = () => {
  return (
    <BarLayout title="" mode="setting">
      <Helmet>
        <title> -   </title>
        <meta name="keywords" content=" -   "></meta>
      </Helmet>
      <Setting />
    </BarLayout>
  );
};

export default Settings;
